import React from "react";
import { useState } from "react";
import logger from "../shared/Console";
import { useDispatch } from "react-redux";
import { useRef } from "react";
import _ from "lodash";

// style
import styled from "styled-components";
import { Grid, Input, Button, Text } from "../elements";
import theme from "../styles/theme";
import { customAlert } from "./Sweet";

const MessageWrite = (props) => {
  const now_message = useRef();
  const msg = now_message.current;

  const { color, border, radius, fontSize } = theme;
  const { sendMessage } = props;

  const [new_message, setMessage] = useState("");

  const changeMessage = (e) => {
    setMessage(e.target.value);
  };

  const sendMessageBtn = () => {
    if (new_message === "") {
      return customAlert.sweetConfirmReload(" .", null, "");
    }
    logger("  ", typeof msg.defaultValue);
    sendMessage(msg.defaultValue);
    setMessage("");
  };

  return (
    <GridTop>
      <Grid is_flex4="t" border={border.bg40} radius={radius.inputBox}>
        <Input
          flex="flex"
          height="4.4rem"
          is_submit
          placeholder=" ."
          type="text"
          length={300}
          onSubmit={sendMessageBtn}
          padding="1.1rem 1.6rem"
          value={new_message}
          _onChange={changeMessage}
          ref={now_message}
          border="none"
        ></Input>
        <Button
          width="6rem"
          padding="0"
          border="none"
          _onClick={sendMessageBtn}
          cursor="pointer"
          bg={color.bg0}
          radius={radius.inputBox}
        >
          <Text color={color.brand100} size={fontSize.small} bg={color.bg0}>
            
          </Text>
        </Button>
      </Grid>
    </GridTop>
  );
};

const GridTop = styled.div`
  @media (min-width: 415px) {
    border-style: solid;
    border-width: 0 1px;
    border-color: #cfcfcf;
    max-width: 36rem;
    margin: 0 auto;
    box-sizing: border-box;
  }
  height: 5rem;
  width: 100%;
  margin: 0 auto;
  padding: 0.3rem;
  background-color: #ffffff;
  position: fixed;
  bottom: 0;
  z-index: 1;
  left: 50%;
  transform: translateX(-50%);
  box-shadow: 0px -2px 6px rgba(0, 0, 0, 0.06);
`;

export default MessageWrite;
